<template>
	<view class="container">
		<view>
			<view class="example-body">
				<uni-nav-bar :fixed="true" color="#333333" background-color="#FFFFFF" @clickLeft="handle_plat" @clickRight="handleSearchOpen">
					<block slot="left"><text class="iconfont iconguanbi f18"></text></block>
					<view class="input-view f18 uni_3030">平城热线</view>
					<block slot="right"><text class="iconfont iconsousuo f22"></text></block>
				</uni-nav-bar>
			</view>
		</view>
		<view class="myAttention mb10">
			<view class="myAttenTap f16" v-for="(item, index) in itemsTap" :key="index" :class="{ myAtten_tap_active: count == index }" @tap="changeTap(index)">{{ item }}</view>
		</view>
		<view class="index_list" :class="count == 0 ? 'dis_tap_show' : ''">
			<!--S 诉求列表 -->
			<view v-for="(item, index) in list" class="uni-divider-line mb15">
				<view @click="handleDetail(item)">
					<view class="uni-flex-list mb15">
						<text class="list_name ">{{ handler_hide(item.submitterName) }}</text>
						<view class="list_num">
							<text>诉求编号：</text>
							<text>{{ item.networkId }}</text>
						</view>
					</view>
					<view class="list_name  font-weigth-none mb15">{{ item.networkContent }}</view>
					<view class="uni-flex-list_img">
						<view class="img" v-for="(itemson, index) in item.networkFile">
							<image :src="imgPath + itemson" mode="aspectFill" alt="" class="uni_width uni_height" @error="handle_image_error"></image>
						</view>
					</view>
				</view>
				<view class="uni-flex-list mb15">
					<text class="list_time">{{ item.submitTime }}</text>
					<!-- <view @click="handleFollow(item)">
						<image v-if="item.isFollow == '1'" src="../../static/images/heart_default.png" mode="aspectFill" class="list_follow uni-vertical-align"></image>
						<image v-if="item.isFollow == '2'" src="../../static/images/heart.png" mode="aspectFill" class="list_follow uni-vertical-align"></image>
						<text class="pl8">{{ item.isFollowDesc }}</text>
					</view> -->
				</view>

				<!--E  诉求列表 -->
			</view>
			<view class="uni-loadmore" v-if="showLoadMore">{{ loadMoreText }}</view>
		</view>
		<view class="index_list" :class="count == 1 ? 'dis_tap_show' : ''">
			<view v-for="item in list" class="uni-divider-line mb15">
				<view @click="handleDetail(item)">
					<view class="uni-flex-list mb15">
						<text class="list_name ">{{ handler_hide(item.submitterName) }}</text>
						<view class="list_num">
							<text>诉求编号：</text>
							<text>{{ item.networkId }}</text>
						</view>
					</view>
					<view class="list_name font-weigth-none mb15">{{ item.networkContent }}</view>
					<view class="uni-flex-list_img">
						<view class="img" v-for="(itemson, index) in item.networkFile">
							<image :src="imgPath + itemson" mode="aspectFit" alt="" class="uni_width uni_height" @error="handle_image_error"></image>
						</view>
					</view>
				</view>
				<view class="uni-flex-list mb15">
					<text class="list_time">{{ item.submitTime }}</text>
					<view @click="handleFollow(item)">
						<image v-if="item.isFollow == '1'" src="../../static/images/heart_default.png" mode="aspectFill" class="list_follow uni-vertical-align"></image>
						<image v-if="item.isFollow == '2'" src="../../static/images/heart.png" mode="aspectFill" class="list_follow uni-vertical-align"></image>
						<text class="pl8">{{ item.isFollowDesc }}</text>
					</view>
				</view>

				<!--E  诉求列表 -->
			</view>
			<view class="uni-loadmore" v-if="showLoadMore">{{ loadMoreText }}</view>
		</view>
	</view>
</template>

<script>
export default {
	components: {},
	data() {
		return {
			count: 0,
			itemsTap: ['我的', '关注'],
			imgPath: this.$config.imgPath,
			params: {
				page: 1,
				limit: 10,
				submitterId: ''
			},
			param_attention: {
				page: 1,
				limit: 10,
				followUser: ''
			},
			list: [],
			imageError: require('../../static/images/error.png'),
			imageList: [],
			loadMoreText: '加载中...',
			showLoadMore: false,
			loadingType: 0
		};
	},
	onTabItemTap(object) {
		if (object.index == 2) {
			uni.showTabBar();
		}
	},
	onLoad() {
		// #ifdef H5
		// this.headerTop = document.getElementsByTagName('uni-page-head')[0].offsetHeight+'px';
		// #endif
		
		this.params.submitterId = uni.getStorageSync('submitterId');
		this.param_attention.followUser = uni.getStorageSync('submitterId');
	},
	onShow() {
		uni.removeStorageSync('keyword')
		this.params.submitterId = uni.getStorageSync('submitterId');
		this.param_attention.followUser = uni.getStorageSync('submitterId');
		this.list=[];
		this.params.page = 1;
		this.param_attention.page=1;
		if(this.count == 0){
			this.loadData_my();
		}else{
			this.loadData_attention()
		}
	},
	onPageScroll(e) {
		//兼容iOS端下拉时顶部漂移
		if (e.scrollTop >= 0) {
			this.headerPosition = 'fixed';
		} else {
			this.headerPosition = 'absolute';
		}
	},
	//下拉刷新
	onPullDownRefresh() {
		this.list = [];
		this.params.page = 1;
		if (this.count == '0') {
			this.loadData_my();
		} else {
			this.loadData_attention();
		}
		uni.stopPullDownRefresh();
	},
	onUnload() {
		this.params.page = 0;
		this.list = [];
		(this.loadMoreText = '加载更多'), (this.showLoadMore = false);
	},
	//加载更多
	onReachBottom() {
		if (this.count == 0) {
			if (!this.showLoadMore) {
				this.showLoadMore = true;
				this.params.page += 1;
				this.loadData_my();
			}
		} else {
			if (!this.showLoadMore) {
				this.showLoadMore = true;
				this.param_attention.page += 1;
				this.loadData_attention();
			}
		}
	},
	methods: {
		/*S 返回*/
		handle_back() {},
		/*E 返回*/
		/*S 导航 搜索按钮 */
		handleSearchOpen() {
			uni.navigateTo({
				url: '/pages/searchAttention/index'
			});
		},
		/*E导航 搜索按钮 */
		/*S 关闭按钮*/
		handle_close() {
			// window.location = 'flutter//goBack'
			if (uni.getSystemInfoSync().platform == 'ios') {
				window.location = 'ios://details';
			} else {
				window.android.finish();
			}
		},
		handle_plat() {
			var _this = this;
			uni.showModal({
				title: '温馨提示',
				content: '是否关闭该应用',
				success: function(res) {
					if (res.confirm) {
						_this.handle_close();
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
		},
		/*e 关闭按钮*/
		handleBack() {
			// window.location = 'flutter//goBack'
			if (uni.getSystemInfoSync().platform == 'ios') {
				window.location = 'ios://details';
			} else {
				window.android.finish();
			}
		},
		/*S 关注初始数据*/
		loadData_attention() {
			this.$minApi.networkWorkOrderFollowPage(this.param_attention).then(res => {
				if (res.data) {
					if (res.data.result.length < this.params.limit) {
						this.showLoadMore = true;
						this.loadMoreText = '没有更多数据了';
					} else {
						this.showLoadMore = false;
					}
					res.data.result.map(i => {
						if (i.networkFile) {
							i.networkFile = JSON.parse(i.networkFile);
						}
					});
					this.list = this.list.concat(res.data.result);
					this.list.map(i => {
						if (i.networkFile) {
							this.imageList = i.networkFile;
						}
					});
				} else {
					this.list = [];
					if (this.params.page > 1) {
						this.params.page -= 1;
					}
					uni.showToast({
						title: '暂无数据',
						duration: 2000
					});
				}

				uni.stopPullDownRefresh();
			});
		},
		/*E 关注初始数据*/
		/*S 我的 初始数据*/

		loadData_my() {
			this.$minApi.networkWorkOrderPage({ ...this.params }).then(res => {
				if (res.data) {
					if (res.data.result.length < this.params.limit) {
						this.showLoadMore = true;
						this.loadMoreText = '没有更多数据了';
					} else {
						this.showLoadMore = false;
					}
					res.data.result.map(i => {
						if (!!i.networkFile) {
							i.networkFile = JSON.parse(i.networkFile);
						}
					});
					this.list = this.list.concat(res.data.result);
					this.list.map(i => {
						if (i.networkFile) {
							this.imageList = i.networkFile;
						}
					});
					uni.stopPullDownRefresh();
				} else {
					this.list = [];
					if (this.params.page > 1) {
						this.params.page -= 1;
					}
					uni.showToast({
						title: '暂无数据',
						duration: 2000
					});
				}
			});
		},
		/*E 关注初始数据*/

		/*S 切换*/

		changeTap(val) {
			console.log(val)
			this.count = val;
			this.params.page = 1;
			this.param_attention.page = 1;
			this.list = [];
			if (val == 1) {
				this.loadData_attention();
			} else {
				this.loadData_my();
			}
		},
		/*E 切换*/

		/*S 图片加载失败 */

		handle_image_error: function(index) {
			this.$set(this.imageList, index, this.imageError);
		},
		/*E 图片加载失败 */
		handleDetail(item) {
			let id = item.networkId;
			let submitterId = this.params.submitterId;
			uni.navigateTo({
				url: `/pages/detail?id=${id}&submitterId=${submitterId}`
			});
		},
		/*S 取消关注 */
		handleFollow(item) {
			var followlist = {
				networkId: item.networkId,
				userId: this.params.submitterId
			};
			this.params.page = 1;
			if (item.isFollow == '2') {
				// 未关注
				this.$minApi.networkWorkOrderCancelFollow({ ...followlist }).then(res => {
					if (res.code == 0) {
						if (this.count == '0') {
							item.isFollow = '1';
							item.isFollowDesc = '未关注';
							uni.showToast({
								title: '取消关注成功'
							});
						} else {
							uni.showToast({
								title: '取消关注成功'
							});
							this.handler_attention_local(item);
						}
					}
				});
			} else {
				// 已关注
				this.$minApi.networkWorkOrderFollow({ ...followlist }).then(res => {
					if (res.code == 0) {
						if (this.count == '0') {
							uni.showToast({
								title: '关注成功'
							});
							item.isFollow = '2';
							item.isFollowDesc = '已关注';
						} else {
							uni.showToast({
								title: '关注成功'
							});
							this.handler_attention_local(item);
						}
					}
				});
			}
		},
		// 局部刷新
		handler_attention_local(item){
			this.list.some((i,index)=>{
				if(i.networkId == item.networkId){
					this.list.splice(index, 1)
					return true;
				}
			})
		},
		/*E 取消关注*/
		/*S 隐藏字符*/
		handler_hide(string) {
			let arrBox = [...string];
			var str = '';
			arrBox.map((res, index) => {
				if (index > 0) {
					str += '*';
				} else {
					str += res;
				}
			});
			return str;
		}
		/*E 隐藏字符*/
	}
};
</script>

<style lang="scss">
.container {
	background-color: #f7f7f7;
	font-size: 24upx;
	.index_banner {
		width: 750upx;
		height: 334upx;
	}
	.index_list {
		display: none;
		padding: 30upx;
		background-color: #ffffff;
	}
	.list_name {
		font-size: 32upx;
		color: #333333;
		font-weight: 500;
	}
	.list_num {
		font-size: 26upx;
		color: #646464;
	}
	.list_time {
		color: #959595;
		font-size: 24upx;
	}
	.list_follow {
		width: 28upx;
		height: 28upx;
	}
	.myAttention {
		padding: 0 176upx;
		display: flex;
		justify-content: space-between;
		align-items: center;
		height: 92upx;
		background-color: #ffffff;
	}
	.myAttenTap {
		width: 120upx;
		color: #767884;
		text-align: center;
	}
	.myAtten_tap_active {
		color: #1b1b1b;
		border-bottom: 4upx solid #3893fe;
	}
	.dis_tap_show {
		display: block;
	}
	.img {
		// border:2upx solid #000000;
		margin-bottom: 10upx;
		margin-right: 5upx;
	}
	.img:nth-child(3n) {
		margin-right: -2upx;
	}
	.uni-flex-list_img {
		display: flex;
		justify-content: flex-start;
		flex-wrap: wrap;
		align-items: center;
	}
	.imageError {
		background: #f5f7fa;
		display: flex;
		justify-content: center;
		align-items: center;
		font-size: 14px;
		color: #c0c4cc;
		vertical-align: middle;
	}
}
</style>
